@import url(app.css);

.circles {
  flex-direction: row;
  margin-top: 5rem;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 4rem 2rem;
  background: linear-gradient(135deg, var(--white) 0%, var(--bg-light) 100%);
}

.circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  background: var(--white);
  width: 220px;
  height: 220px;
  border-radius: var(--radius-full);
  box-shadow: var(--shadow-lg);
  border: 3px solid transparent;
  background: linear-gradient(var(--white), var(--white)) padding-box,
    var(--bg-primary) border-box;
  position: relative;
  overflow: hidden;
  transition: all var(--transition-normal);
  animation: float 6s ease-in-out infinite;
}

.circle::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg,
    transparent,
    rgba(37, 99, 235, 0.1),
    transparent
  );
  transform: rotate(45deg);
  transition: all var(--transition-slow);
  opacity: 0;
}

.circle:hover::before {
  opacity: 1;
  animation: shimmer 2s ease-in-out;
}

.circle:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: var(--shadow-xl);
}

.counter {
  font-size: 2.5rem;
  font-weight: 800;
  font-family: var(--font-heading);
  background: var(--bg-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
  position: relative;
  z-index: 2;
}

.circle-name {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-align: center;
  position: relative;
  z-index: 2;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%) translateY(-100%) rotate(45deg);
  }
  100% {
    transform: translateX(100%) translateY(100%) rotate(45deg);
  }
}

@media screen and (max-width: 1024px) {
  .circles {
    gap: 2rem;
  }

  .circle {
    width: 180px;
    height: 180px;
  }

  .counter {
    font-size: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .circles {
    flex-wrap: wrap;
    gap: 2rem;
    margin-top: 3rem;
  }

  .circle {
    width: 150px;
    height: 150px;
  }

  .counter {
    font-size: 1.75rem;
  }

  .circle-name {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 480px) {
  .circles {
    gap: 1.5rem;
    padding: 2rem 1rem;
  }

  .circle {
    width: 120px;
    height: 120px;
  }

  .counter {
    font-size: 1.5rem;
  }

  .circle-name {
    font-size: 0.8rem;
  }
}
