@import url(app.css);

.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4rem;
  padding: 120px 2rem 4rem;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(37, 99, 235, 0.05) 0%,
    rgba(6, 182, 212, 0.05) 100%
  );
  z-index: -1;
}

.hero-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  animation: fadeInUp 1s ease-out;
}

.hero-content h1 {
  font-size: clamp(2.5rem, 5vw, 4rem);
  font-weight: 800;
  font-family: var(--font-heading);
  line-height: 1.1;
  background: linear-gradient(
    135deg,
    var(--text-primary) 0%,
    var(--primary-color) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0;
}

.hero-content p {
  font-size: 1.25rem;
  line-height: 1.6;
  color: var(--text-secondary);
  max-width: 500px;
  margin: 0;
}

.hero-cta {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-top: 1rem;
}

.hero-cta .btn {
  font-size: 1rem;
  padding: 1rem 2rem;
}

.hero-cta .btn-secondary {
  background: var(--white);
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.hero-cta .btn-secondary:hover {
  background: var(--primary-color);
  color: var(--white);
}

.hero-img {
  flex: 1;
  position: relative;
  animation: fadeInRight 1.2s ease-out;
}

.hero-img img {
  width: 100%;
  height: auto;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-xl);
  transition: all var(--transition-normal);
}

.hero-img img:hover {
  transform: scale(1.02);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

.hero-stats {
  position: absolute;
  bottom: -2rem;
  left: 2rem;
  background: var(--white);
  padding: 1.5rem;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  display: flex;
  gap: 2rem;
  animation: fadeInUp 1.4s ease-out;
}

.stat-item {
  text-align: center;
}

.stat-number {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-color);
  display: block;
}

.stat-label {
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin-top: 0.25rem;
}

.about {
  display: flex;
  margin: 0 auto;
  align-items: center;
  gap: 4rem;
  justify-content: space-between;
  padding: 4rem 2rem;
  max-width: 1200px;
}

.about-content {
  flex: 1;
  animation: fadeInLeft 1s ease-out;
}

.about-content p {
  font-size: 1.125rem;
  line-height: 1.7;
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.about-img {
  flex: 1;
  animation: fadeInRight 1s ease-out;
}

.about-img img {
  width: 100%;
  height: auto;
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  transition: all var(--transition-normal);
}

.about-img img:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-xl);
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media screen and (max-width: 1024px) {
  .hero {
    flex-direction: column;
    text-align: center;
    gap: 3rem;
    padding: 140px 2rem 6rem;
  }

  .hero-content h1 {
    font-size: 3rem;
  }

  .hero-stats {
    position: static;
    margin-top: 2rem;
    justify-content: center;
  }

  .about {
    flex-direction: column;
    gap: 3rem;
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .hero {
    padding: 120px 1rem 4rem;
  }

  .hero-content h1 {
    font-size: 2.5rem;
  }

  .hero-content p {
    font-size: 1.1rem;
  }

  .hero-cta {
    justify-content: center;
  }

  .hero-stats {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
  }

  .about {
    padding: 3rem 1rem;
  }
}

@media screen and (max-width: 480px) {
  .hero-content h1 {
    font-size: 2rem;
  }

  .hero-cta {
    flex-direction: column;
    align-items: center;
  }

  .hero-cta .btn {
    width: 100%;
    max-width: 300px;
  }
}
